nav {
  position: fixed;
  top:0;
  width: 100%;
  height: 50px;
  background: midnightblue;
}

.list {
  list-style-type: none;
  background: midnightblue;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  letter-spacing: 2px;
  font-size: 22px;
}

.items{
  margin-right: 20px;
  text-transform: uppercase;
  color: #f1f1f1;
  cursor: pointer;
}



.btn{
  display: none;
  position: absolute;
  right: 10px;
  top: 10px;
  padding: 5px;
  color: #fff;
  height: 30px;
  cursor: pointer;
  background: rgb(75, 158, 197);
  border-radius: 5px;
  border: none;
  letter-spacing: 1px;
}

@media screen and (max-width: 500px){
  .list{
    flex-direction: column;
    height: auto;
  }

  .items:nth-child(1){
    border-top: 1px solid rgba(255,255,255,0.6);
    margin-top: 50px;
  }
  
  .items{
    line-height: 70px;
    height: 70px;
    width: 100%;
    border-top: 1px solid rgba(255,255,255,0.6);
    text-align: center;
  }

  .btn{
    display: block;
    right: auto;
    left: 10px;
  }
}