.card{
  max-width: 300px;
  padding: 10px;
  box-shadow: 0 5px 15px rgba(0,0,0,0.3);
  margin: 10px;
  cursor: pointer;
  min-height: 200px;
  min-width: 300px;
  text-align: left;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border-radius: 5px;
}

.card h2{
  font-size: 25px;
  margin-bottom: 10px;
}

.card p{
  font-size: 18px;
}